<template>
    <div class="notice-wrap">
        <div v-title data-title="爱股轩公告">{{text}}</div>
        <header class="header"><span @click="Return"><</span>{{text}}</header>
        <div class="content-wrap">
            <ul class="content">
                <li v-for="item in items" :key="item.id">
                    <img v-lazy="dataImg">
                    <p>{{item}}</p>
                </li>
            </ul>
        </div>  
    </div>
</template>
<script>
    export default {
        name : 'notice',
        data(){
            return {
                text:'爱股轩公告',
                items:['客服人员周末值班安排','关于客户三证合一、开票资料变更的通知','受台风影响配送延迟通知','国庆节放假期间客服值班安排'],
                dataImg:require('@/assets/notice-icon.jpg')
            }
        },
        methods:{
            Return:function(){
                this.$router.go(-1);
            }
        }
    }
</script>
<style lang="less" scoped>
    .notice-wrap{
        .header{
            width:100%;
            height:.9rem;
            background: -webkit-linear-gradient(left, #f97522, #fd5148);
            background: -o-linear-gradient(right, #f97522, #fd5148);
            background: -moz-linear-gradient(right, #f97522, #fd5148);
            background: linear-gradient(to right, #f97522, #fd5148);
            text-align:center;
            line-height:.9rem;
            color:#fff;
            font-size:.36rem;
            position:relative;
            span{
                width:.46rem;
                height:.46rem;
                border:1px solid #fff;
                border-radius:50%;
                display:block;
                position:absolute;
                left:.2rem;
                top:50%;
                margin-top:-.23rem;
                text-align:center;
                line-height:.46rem;
                font-family:'宋体';
                font-size:.4rem;
            }
        }
        .content-wrap{
             padding-top:.2rem;
             background:#f2f2f2;
            .content{
                background:#fff;
                li{
                    background:#fff;
                    padding:.2rem 0;
                    margin:0 .2rem;
                    border-bottom:1px solid #dfdfdd;
                    position:relative;
                    height:.53rem;
                    line-height:.53rem;
                    padding-left:.7rem;
                    img{
                        width:.53rem;
                        height:.53rem;
                        position:absolute;
                        left:0;
                        top:.2rem;
                    }
                    p{
                        font-size:.32rem;
                        color:#333333;
                        line-height:.53rem;
                        width:6.4rem;
                        overflow: hidden;
                        text-overflow:ellipsis;
                        white-space: nowrap;
                    }
                }
            }
        }
    }
</style>


